@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
@import url(https://fonts.googleapis.com/css?family=Roboto);
.text-weight {
  width: 580px;
  margin-left: auto;
  margin-right: auto;
}
/* animation views replace */
/* Header */
.header {
  padding: 15px 30px;
  position: absolute;
  width: 100%;
  display: inline-block;
  right: 0;
  left: 0;
  z-index: 50;
  overflow-x: hidden;
  top: -85px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header .nav {
  display: inline-block;
  list-style: none;
  float: none;
}
.header .nav li a {
  margin-right: 1.5em;
  text-align: center;
  font-size: 1.3em;
  border-radius: 0em;
  padding: 0;
}
.header .nav li a:after {
  margin-top: 25px;
  position: absolute;
  left: 0;
  width: 0%;
  height: 2px;
  content: '';
  opacity: 0;
  transition: width 0.3s, opacity 0.2s, transform 0.3s;
}
.header .nav li a:hover::after,
.header .nav li a:focus::after {
  opacity: 1;
  width: 100%;
  text-decoration: none;
}
@media only screen and (min-width: 600px) {
  .header .nav li a {
    font-size: 1em;
    margin-left: 2.5em;
    transition: font-size 3s ease 0s;
  }
}
@media only screen and (min-width: 600px) {
  .header .nav {
    float: right;
  }
}
.header.transparent {
  background: none !important;
  border: none;
  box-shadow: none !important;
}
.header.in {
  top: 0px;
  transition: all 0.6s ease 0s;
}
@media only screen and (min-width: 600px) {
  .header.in {
    top: 0px;
  }
}
.header.out {
  top: -85px;
  transition: all 0.6s ease 0s;
}
@media only screen and (min-width: 600px) {
  .header.out {
    top: -104px;
  }
}
.header.black {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.08);
  background: rgba(239, 238, 238, 0.9);
}
.header.black li a:after {
  background: #000;
}
@media only screen and (min-width: 960px) {
  .header.black li a {
    color: #393939;
  }
}
.header.white li a {
  color: #fff;
}
.header.white li a:after {
  background: #fff;
}
.header.white li.active a,
.header.white li a:hover,
.header.white li.active a:hover {
  background-color: inherit;
  color: #fff;
  transition: color 1s ease 0s;
}
.header.black li.active a,
.header.black li a:hover,
.header.black li.active a:hover {
  background-color: inherit;
  color: #000;
  transition: color 1s ease 0s;
}
/* main index */
/* animation view */
.animate-home.ng-enter {
  animation: 1s fade-in-home;
  animation-timing-function: ease;
  z-index: 100;
}
@keyframes fade-in-home {
  from {
    opacity: 0;
    transform: scale(0.1, 0.1);
  }
  to {
    opacity: 1;
    transform: scale(1, 1);
  }
}
.animate-home.ng-leave {
  animation: 1s fade-out-home;
  animation-timing-function: ease;
  z-index: 99;
}
@keyframes fade-out-home {
  from {
    opacity: 1;
    transform: scale(1, 1);
  }
  to {
    opacity: 0;
    transform: scale(0.1, 0.1);
  }
}
/* Jumbotrom */
.home.main {
  /* start reading */
}
