}
@media screen and (min-width: 700px) {
  .home.main .start_reading h4 {
    width: 580px;
    margin-left: auto;
    margin-right: auto;
  }
}
.home.main .start_reading a {
  text-decoration: underline;
  cursor: pointer;
  color: #000;
  transition: color 0.2s ease 0s;
}
.home.main .start_reading a:hover {
