/* animation view */
.animate-skills.ng-enter {
  animation: 1s fade-in-skill;
  animation-timing-function: ease;
  z-index: 100;
}
@keyframes fade-in-skill {
  from {
    opacity: 0;
    transform: skewY(-15deg);
  }
  to {
    opacity: 1;
    transform: skewY(0deg);
  }
}
.animate-skills.ng-leave {
  animation: 1s fade-out-skill;
  animation-timing-function: ease;
  z-index: 99;
}
@keyframes fade-out-skill {
  from {
    opacity: 1;
    transform: scale(1, 1);
  }
  to {
    opacity: 0;
    transform: scale(0.1, 0.1);
  }
}
/* main-2 skills */
.skills .col-md-3 {
  height: 165px;
  margin-bottom: 4em;
}
.skills .col-md-3 img {
  transform: translate(30px, 0);
}
.skills .row:nth-child(2) .col-md-3:nth-child(1) img {
  margin: 2.5px 0;
}
.skills .row:nth-child(2) .col-md-3:nth-child(2) img {
  margin: 30px 0;
}
.skills .row:nth-child(2) .col-md-3:nth-child(3) img {
  margin: 7.5px 0;
}
.skills .row:nth-child(5) .col-md-3:nth-child(1) img {
  margin: 22.5px 0;
}
.skills .row:nth-child(5) .col-md-3:nth-child(4) img {
  margin: 3.5px 0;
}
.skills .row:nth-child(7) .col-md-3:nth-child(2) img {
  margin: 12.5px 0;
}
.skills .row:nth-child(7) .col-md-3:nth-child(3) img {
  margin: 22px 0;
}
.skills h2 {
  font-family: 'Indie Flower', cursive;
  padding: 30px;
  border-bottom: 1px solid #000;
  border-top: 1px solid #000;
  opacity: 0;
  font-size: 3em;
  padding: 0;
  margin: 0 0 1.5em 0;
  transform: translate(0, 20px);
}
.skills.init_2 h2 {
  opacity: 1;
  transition: all 1s ease 0s;
  transform: translate(0px, 0px);
}
.skills.init_2 .row .col-md-3 img {
  transition: all 1s ease 0s;
  transform: translate(0px, 0px);
}
/* animation view */
.animate-projects.ng-enter {
  animation: 1s fade-in-projects;
  animation-timing-function: ease-out;
  z-index: 100;
}
@keyframes fade-in-projects {
  from {
    opacity: 0;
    transform: translateY(1000px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
.animate-projects.ng-leave {
  animation: 1s fade-out-projects;
  animation-timing-function: ease-out;
  z-index: 99;
}
@keyframes fade-out-projects {
  from {
    opacity: 1;
    transform: translateY(0px);
  }
  to {
    opacity: 0;
    transform: translateY(1000px);
  }
}
/*main-2 projects*/
.projects .row {
  margin-top: 30px;
}
.projects a {
  display: block;
}
.projects a:hover {
  opacity: 1;
  background-color: rgba(255, 255, 255, 0.8);
  transform: scale(1);
}
.projects a:hover img {
  opacity: .2;
}
.projects a:hover div {
  opacity: 1;
  transform: scale(1);
}
.projects .descr {
  position: absolute;
  top: 0;
  padding: 20px;
  opacity: 0;
  color: #204056;
  text-align: left;
  line-height: 2;
  transition: transform 0.15s ease-in-out, opacity 0.15s ease-in-out;
  transform-origin: 50% 50%;
  transform: scale(0.95);
}
/* Footer */
.footer {
  padding-top: 2.5em;
  background: #efeeee;
  border-top: 1px solid rgba(0, 0, 0, 0.05);
}
.footer ul {
  list-style: none;
  margin: 0;
  padding: 0;
  float: left;
}
.footer ul li {
  display: inline-block;
  margin: 0 40px 0 0;
}
.footer p {
  text-align: right;
}
.projects .footer {
  margin-top: 3em;
}
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
}
body {
  background-color: #efeeee;
  font-size: 100%;
  line-height: 1.75em;
  color: #393939;
  font-weight: 500;
  text-align: center;
  overflow: hidden !important;
  -webkit-overflow-scrolling: touch;
}
@media screen and (min-width: 600px) {
  body {
    font-size: 150%;
  }
}
.main {
  margin: 0px;
  width: 100%;
  height: auto;
  overflow: auto;
  display: block;
  position: relative;
}
.view-container {
  position: relative;
}
.animate-home.ng-enter,
.animate-home.ng-leave,
.animate-skills.ng-enter,
.animate-skills.ng-leave,
.animate-projects.ng-enter,
.animate-projects.ng-leave {
  position: absolute;
  top: 0px;
  left: 0;
  right: 0;
}
.skills,
.projects {
  padding: 8em 0 0 0;
}
@media only screen and (min-width: 600px) {
  .skills,
  .projects {
    padding: 5em 0 0 0;
  }
}
